import Layout from '../components/MyLayout'
import Link from 'next/link'
import fetch from 'isomorphic-unfetch'


const Home = (props) => (
  <Layout>
      <h1>Countries:</h1>
      <ul>
      {props.data.map(({name}) => (
        <li key={name}>
          <Link as={`/country/${name.name}`} href={`/country?id=${name.name}`}>
            <a >{name}</a>
          </Link>
        </li>
      ))}
    </ul>
 <style jsx>{`

        li {
          list-style: none;
          margin: 8px 0;
        }

        a {
          text-decoration: none;
          color: white;
        }

        a:hover {
          opacity: 0.6;
        }
      `}</style>
    </Layout>
)



Home.getInitialProps = async function() {
  const res = await fetch ('https://restcountries.eu/rest/v2/region/asia/?q=name')
  const data = await res.json();

  console.log(`Data fetched. Count: ${data.length}`)
  
  return {
     data
     }
}

 export default Home;
